	module.exports = {"data":{"challengeNode":{"title":"Problem 346: Strong Repunits","description":["The number 7 is special, because 7 is 111 written in base 2, and 11 written in base 6 (i.e. 710 = 116 = 1112). In other words, 7 is a repunit in at least two bases b > 1. ","","","We shall call a positive integer with this property a strong repunit. It can be verified that there are 8 strong repunits below 50:  {1,7,13,15,21,31,40,43}. Furthermore, the sum of all strong repunits below 1000 equals 15864.","","Find the sum of all strong repunits below 1012."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-346-strong-repunits","blockName":"Project Euler","tests":[{"text":"<code>euler346()</code> should return 336108797689259260.","testString":"assert.strictEqual(euler346(), 336108797689259260, '<code>euler346()</code> should return 336108797689259260.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler346() {\n  // Good luck!\n  return true;\n}\n\neuler346();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-347-largest-integer-divisible-by-two-primes","id":"5900f4c71000cf542c50ffd8"},"slug":"/coding-interview-prep/project-euler/problem-346-strong-repunits"}}

